import { createApplication } from 'lath'
import frameworksConfig from './frameworks/minify'
import frameworksHTML from './frameworks/source'
import home from './home/minify'
import tutorial from './tutorial/minify'
import api from './api/minify'
import doc from './doc/minify'
import typesDoc from './typesDoc/minify'
import empty from './empty/minify'
import sheetsBottom from './sheetsBottom/minify'
import './components/menu-icon'
import './components/app-nav'
import './components/app-logo'
import './components/menu-list'
import './components/dark-switch'
import './components/lang-switch'

document.body.innerHTML = `
  <define-application default-applet="frameworks">
    <define-applet applet-id="frameworks">
      ${frameworksHTML}
    </define-applet>
  </define-application>
`

createApplication({
  applets: {
    home,
    tutorial,
    frameworks: frameworksConfig,
    doc,
    api,
    empty,
    typesDoc,
    sheetsBottom
  }
}).then(({ application }) => {
  window['app'] = application
  window['_slideTo'] = (index = 0) => {
    application.applets.frameworks.slide?.to(index)
  };
})

document.body.style.margin = '0'
document.body.style.overflow = 'hidden'

